.book-cover{display:flex;flex-shrink:0;align-items:center;justify-content:center;width:38px;height:55px;margin:0;overflow:hidden}.book-cover.book-cover_details{width:102px;height:144px}.book-cover::before{content:'';visibility:hidden;opacity:0}.book-cover_loading{position:relative;background:rgba(0,0,0,0.1);border:1px solid rgba(0,0,0,0.1)}.book-cover_loading::before{position:absolute;visibility:visible;width:300%;height:20px;margin-left:-40%;box-shadow:0 -30px 10px #fff;opacity:1;-webkit-animation:loadingAnimation 2s linear infinite;animation:loadingAnimation 2s linear infinite}.book-cover_loading.book-cover_details::before{height:40px;box-shadow:0 -60px 20px #fff}.book-cover--img{max-width:100%}@-webkit-keyframes loadingAnimation{from{-webkit-transform:translateY(-200%) rotate(-10deg);transform:translateY(-200%) rotate(-10deg)}to{-webkit-transform:translateY(400%) rotate(-10deg);transform:translateY(400%) rotate(-10deg)}}@keyframes loadingAnimation{from{-webkit-transform:translateY(-200%) rotate(-10deg);transform:translateY(-200%) rotate(-10deg)}to{-webkit-transform:translateY(400%) rotate(-10deg);transform:translateY(400%) rotate(-10deg)}}

