.top-bar--wrapper{display:flex;width:100%;padding:20px;box-shadow:0 5px 10px rgba(0,0,0,0.1)}.top-bar--title-wrapper{display:flex;align-items:center;margin:0 20px 0 0}.top-bar--title{flex-shrink:0;margin:0;font-size:30px}@media (max-width: 1024px){.top-bar--wrapper{padding:10px}.top-bar--title{font-size:20px}}@media (max-width: 560px){.top-bar--wrapper{flex-direction:column}.top-bar--title-wrapper{justify-content:center;margin:0 0 10px 0}}

