slateY(-55px) translateX(-85px) translateZ(1px);transform:translateY(-55px) translateX(-85px) translateZ(1px);-webkit-animation:leftPageAnimation 1s linear infinite;animation:leftPageAnimation 1s linear infinite}.loading-indiator--book-page.right{background-color:#eee;-webkit-transform:rotate3d(0, 1, 0, 90deg) translateY(-55px) translateX(-85px) translateZ(-1px);transform:rotate3d(0, 1, 0, 90deg) translateY(-55px) translateX(-85px) translateZ(-1px)}.loading-indiator--book-page.middle{background-color:#eee;-webkit-transform:rotate3d(0, 1, 0, 90deg) translateY(-55px) translateX(-85px) translateZ(-1px);transform:rotate3d(0, 1, 0, 90deg) translateY(-55px) translateX(-85px) translateZ(-1px);-webkit-animation:middlePageAnimation 1s linear infinite;animation:middlePageAnimation 1s linear infinite}@-webkit-keyframes middlePageAnimation{0%{background-color:#eee;-webkit-transform:rotate3d(0, 1, 0, 90deg) translateY(-55px) translateX(-85px) translateZ(-1px);transform:rotate3d(0, 1, 0, 90deg) translateY(-55px) translateX(-85px) translateZ(-1px)}50%{background-color:#fff;-webkit-transform:rotate3d(0, 1, 0, 0deg) translateY(-55px) translateX(-85px) translateZ(1px);transform:rotate3d(0, 1, 0, 0deg) translateY(-55px) translateX(-85px) translateZ(1px)}100%{background-color:#fff;-webkit-transform:rotate3d(0, 1, 0, 0deg) translateY(-55px) translateX(-85px) translateZ(1px);transform:rotate3d(0, 1, 0, 0deg) translateY(-55px) translateX(-85px) translateZ(1px)}}@keyframes middlePageAnimation{0%{background-color:#eee;-webkit-transform:rotate3d(0, 1, 0, 90deg) translateY(-55px) translateX(-85px) translateZ(-1px);transform:rotate3d(0, 1, 0, 90deg) translateY(-55px) translateX(-85px) translateZ(-1px)}50%{background-color:#fff;-webkit-transform:rotate3d(0, 1, 0, 0deg) translateY(-55px) translateX(-85px) translateZ(1px);transform:rotate3d(0, 1, 0, 0deg) translateY(-55px) translateX(-85px) translateZ(1px)}100%{background-color:#fff;-webkit-transform:rotate3d(0, 1, 0, 0deg) translateY(-55px) translateX(-85px) translateZ(1px);transform:rotate3d(0, 1, 0, 0deg) translateY(-55px) translateX(-85px) translateZ(1px)}}@-webkit-keyframes leftPageAnimation{0%{box-shadow:inset 0 0 0 rgba(0,0,0,0.1)}50%{box-shadow:inset -50px 0 20px rgba(0,0,0,0.05)}100%{box-shadow:inset -50px 0 20px rgba(0,0,0,0.05)}}@keyframes leftPageAnimation{0%{box-shadow:inset 0 0 0 rgba(0,0,0,0.1)}50%{box-shadow:inset -50px 0 20px rgba(0,0,0,0.05)}100%{box-shadow:inset -50px 0 20px rgba(0,0,0,0.05)}}

